import { Component, Input, Output } from '@angular/core';
import { IBreadcrumb } from '../../interfaces/components/breadcrumb.interface';
import { CommonModule } from '@angular/common';
import { Router } from '@angular/router';

@Component({
  selector: 'custom-breadcrumb',
  imports: [CommonModule],
  templateUrl: './custom-breadcrumb.component.html',
  styleUrl: './custom-breadcrumb.component.css'
})
export class CustomBreadcrumbComponent {
  @Input({required : true}) breadcrumbItems: Array<IBreadcrumb> = [];
  @Output() breadcrumbItemClicked: (item: IBreadcrumb) => void = (item: IBreadcrumb) => {
    // route to url
    console.log('Breadcrumb item clicked:', item);
    this.router.navigate([item.url]);
  };

  constructor(private router: Router) {}

}
