import { CommonModule } from '@angular/common';
import { Component, EventEmitter, HostListener, Input, Output } from '@angular/core';

@Component({
  selector: 'custom-toggle-switch',
  imports: [CommonModule],
  templateUrl: './custom-toggle-switch.component.html',
  styleUrl: './custom-toggle-switch.component.css'
})
export class CustomToggleSwitchComponent {
  @Input({required : true}) value: boolean = false;
  @Input() label: string = '';
  @Input() labelPosition: 'left' | 'right' = 'right';
  @Input() disabled: boolean = false;
  @Input({required : true}) size: 'small' | 'medium' | 'large' = 'medium';
  @Input() onColor: string = '#4CAF50';
  @Input() offColor: string = '#F43F5E';
  @Input() thumbColor: string = '#ffffff';

  @Output() valueChange = new EventEmitter<boolean>();

  @HostListener('click')
  toggle() {
    if (!this.disabled) {
      this.value = !this.value;
      this.valueChange.emit(this.value);
    }
  }
}
