import { Component, HostBinding, Input } from '@angular/core';

@Component({
  selector: 'custom-svg-icon',
  imports: [],
  templateUrl: './custom-svg-icon.component.html',
  styleUrl: './custom-svg-icon.component.css'
})
export class CustomSvgIconComponent {
  @HostBinding('style.-webkit-mask-image') private _path!: string;

  @Input() public set path(filePath: string) {
    this._path = `url("${filePath}")`;
  }
}
