import { Component, EventEmitter, Input, Output } from '@angular/core';
import { FormGroup, ReactiveFormsModule } from '@angular/forms';
import { CustomAppErrorComponent } from '../custom-app-error/custom-app-error.component';
import { IComponentFormError } from '../../interfaces';
import { ComponentFormErrorConstant } from '../../enums';
import { ClickOutsideDirective } from '../../directives';
import { CommonModule } from '@angular/common';

@Component({
  selector: 'custom-calender-form',
  standalone: true,
  imports: [
    CustomAppErrorComponent,
    ClickOutsideDirective,
    ReactiveFormsModule,
    CommonModule,
  ],
  templateUrl: './custom-calender-form.component.html',
  styleUrls: ['./custom-calender-form.component.css'],
})
export class CustomCalenderFormComponent {
  // TODO: implement ControlValueAccessor properly
  // Inputs
  @Input() label: string = '';
  @Input() placeholder: string = 'Select date';
  @Input() labelClass: string = '';
  @Input() calendarPopUpClass: string = '';
  @Input() calendarInputClass: string = '';
  @Input() calendarContainerClass: string = '';
  @Input() componentClass: string = '';
  @Input() minDate: Date | null = null;
  @Input() maxDate: Date | null = null;
  @Input({ required: true }) controlName: string = '';
  @Input({ required: true }) parentForm!: FormGroup;
  @Input({ required: true }) validation: IComponentFormError[] = [];
  @Input({ required: true }) name: string = '';
  @Input() disabled: boolean = false;
  @Output() valueChange = new EventEmitter<Date | null>();

  // Calendar state
  showCalendarForm: boolean = false;
  currentMonth: Date = new Date();
  days: Date[] = [];
  weekdays = ['Su', 'Mo', 'Tu', 'We', 'Th', 'Fr', 'Sa'];

  // ControlValueAccessor implementation
  private _value: Date | null = null;
  private onChange: any = () => {};
  private onTouched: any = () => {};

  constructor() {
    this.generateCalendar();
  }

  ngOnInit(): void {
    if (this.parentForm && this.controlName) {
      const control = this.parentForm.get(this.controlName);
      if (control) {
        control.valueChanges.subscribe((value: any) => {
          this.value = value;
        });
      }
    }
  }

  // Getters and setters
  get value(): Date | null {
    return this._value;
  }

  set value(val: Date | null) {
    this._value = val;
    this.onChange(val);
    this.onTouched();
  }

  // ControlValueAccessor methods
  writeValue(value: Date | null): void {
    this._value = value;
  }

  registerOnChange(fn: any): void {
    this.onChange = fn;
  }

  registerOnTouched(fn: any): void {
    this.onTouched = fn;
  }

  // Calendar navigation
  toggleCalendar(): void {
    this.showCalendarForm = !this.showCalendarForm;
  }

  prevMonth(): void {
    this.currentMonth = new Date(
      this.currentMonth.getFullYear(),
      this.currentMonth.getMonth() - 1,
      1
    );
    this.generateCalendar();
  }

  nextMonth(): void {
    this.currentMonth = new Date(
      this.currentMonth.getFullYear(),
      this.currentMonth.getMonth() + 1,
      1
    );
    this.generateCalendar();
  }

  // Calendar generation
  generateCalendar(): void {
    this.days = [];
    const year = this.currentMonth.getFullYear();
    const month = this.currentMonth.getMonth();

    const firstDay = new Date(year, month, 1);
    const lastDay = new Date(year, month + 1, 0);

    // Previous month days
    const prevMonthDays = firstDay.getDay();
    for (let i = prevMonthDays - 1; i >= 0; i--) {
      this.days.push(new Date(year, month, -i));
    }

    // Current month days
    for (let i = 1; i <= lastDay.getDate(); i++) {
      this.days.push(new Date(year, month, i));
    }

    // Next month days
    const nextMonthDays = 6 - lastDay.getDay();
    for (let i = 1; i <= nextMonthDays; i++) {
      this.days.push(new Date(year, month + 1, i));
    }
  }

  // Date selection
  selectDate(date: Date): void {
    if (!this.isDisabled(date)) {
      this.value = date;
      this.showCalendarForm = false;
      if (this.parentForm && this.controlName) {
        this.parentForm.get(this.controlName)?.setValue(date);
        this.parentForm.get(this.controlName)?.markAsTouched();
      }
    }
    this.valueChange.emit(date);
  }

  // Utility methods
  isSelected(date: Date): boolean {
    if (!this.value) return false;
    return (
      date.getDate() === this.value.getDate() &&
      date.getMonth() === this.value.getMonth() &&
      date.getFullYear() === this.value.getFullYear()
    );
  }

  isCurrentMonth(date: Date): boolean {
    return date.getMonth() === this.currentMonth.getMonth();
  }

  isDisabled(date: Date): boolean {
    const isBeforeMin = this.minDate ? date < this.minDate : false;
    const isAfterMax = this.maxDate ? date > this.maxDate : false;
    return isBeforeMin || isAfterMax;
  }

  getMonthName(): string {
    return this.currentMonth.toLocaleString('default', { month: 'long' });
  }

  getYear(): number {
    return this.currentMonth.getFullYear();
  }

  formatDisplayDate(): string {
    if (!this.value) return '';
    try {
      return this.value.toLocaleDateString();
    } catch (error) {
      return '';
    }
  }

  containRequiredError() {
    return this.validation.some((error) =>
      error.errorType.includes(ComponentFormErrorConstant.REQUIRED)
    );
  }
}
