import {
  Component,
  EventEmitter,
  Input,
  Output,
  SimpleChanges,
} from '@angular/core';
import {
  ControlValueAccessor,
  FormGroup,
  FormsModule,
  ReactiveFormsModule,
} from '@angular/forms';
import { ClickOutsideDirective } from '../../directives';
import { CustomAppErrorComponent } from '../custom-app-error/custom-app-error.component';
import { IComponentFormError, IDropdownOption } from '../../interfaces';
import { ComponentFormErrorConstant } from '../../enums';

@Component({
  selector: 'custom-dropdown-form',
  standalone: true,
  imports: [
    FormsModule,
    ReactiveFormsModule,
    ClickOutsideDirective,
    CustomAppErrorComponent,
  ],
  templateUrl: './custom-dropdown-form.component.html',
  styleUrls: ['./custom-dropdown-form.component.css'],
})
export class CustomDropdownFormComponent {
  @Input({ required: true }) parentForm!: FormGroup | any;
  @Input({ required: true }) controlName!: string;
  @Input() label?: string;
  @Input() labelClass: string = '';
  @Input() dropdownOptionsClass: string = '';
  @Input() dropdownHeaderClass: string = '';
  @Input() dropdownContainerClass: string = '';
  @Input() placeholder: string = 'Select an option';
  @Input() enableFilter: boolean = false;
  @Input() showClear: boolean = true;
  @Input({ required: true }) options: IDropdownOption[] = [];
  @Input({ required: true }) name: string = '';
  @Input({ required: true }) validation: IComponentFormError[] = [];
  @Input() disabled: boolean = false;
  @Output() valueChange: EventEmitter<IDropdownOption | null> =
    new EventEmitter<IDropdownOption | null>();
  isOpen: boolean = false;
  selectedOption: IDropdownOption | null = null;
  filteredOptions: IDropdownOption[] = [];
  filterText: string = '';
  public value: any;

  ngOnInit() {
    this.filteredOptions = [...this.options];
    this.setupFormControlSubscription();
  }

  ngOnChanges(changes: SimpleChanges) {
    // This will trigger when options are loaded from the parent
    if (changes['options'] && changes['options'].currentValue) {
      this.filteredOptions = [...this.options];
      this.updateSelectedOptionFromFormValue();
    }
  }

  private setupFormControlSubscription() {
    if (this.parentForm && this.controlName) {
      const control = this.parentForm.get(this.controlName);
      if (control) {
        // Set initial value
        if (control.value) {
          this.value = control.value;
          this.updateSelectedOptionFromFormValue();
        }

        // Subscribe to future changes
        control.valueChanges.subscribe((value: any) => {
          this.value = value;
          this.updateSelectedOptionFromFormValue();
        });
      }
    }
  }

  private updateSelectedOptionFromFormValue() {
    const currentValue = this.parentForm.get(this.controlName)?.value;
    // console.log("Updating selected option:", {
    //   currentValue,
    //   options: this.options,
    //   controlName: this.controlName,
    // });

    if (currentValue && this.options.length > 0) {
      this.selectedOption =
        this.options.find((opt) => opt.id === currentValue) || null;
      // console.log("Found selected option:", this.selectedOption);
    } else {
      this.selectedOption = null;
    }
  }

  toggleDropdown() {
    this.isOpen = !this.isOpen;
    if (this.isOpen) {
      this.filterText = '';
      this.filterOptions();
    }
  }

  writeValue(value: any): void {
    this.value = value;
  }

  selectOption(option: IDropdownOption) {
    // console.log("option", option);
    this.selectedOption = option;
    this.parentForm.get(this.controlName)?.setValue(option.id);
    this.isOpen = false;
    this.valueChange.emit(option);
  }

  clearSelection(event: Event) {
    event.stopPropagation();
    this.selectedOption = null;
    this.parentForm.get(this.controlName)?.setValue(null);
    this.valueChange.emit(null);
  }

  filterOptions() {
    if (!this.filterText) {
      this.filteredOptions = [...this.options];
    } else {
      const searchText = this.filterText.toLowerCase();
      this.filteredOptions = this.options.filter((option) =>
        option.nameEn.toLowerCase().includes(searchText)
      );
    }
  }

  @Input() set reset(value: boolean) {
    if (value) {
      this.selectedOption = null;
      this.filterText = '';
      this.filterOptions();
    }
  }

  containRequiredError() {
    return this.validation.some((error) =>
      error.errorType.includes(ComponentFormErrorConstant.REQUIRED)
    );
  }
}
