import { Component, EventEmitter, Input, Output } from '@angular/core';
import { FormsModule } from '@angular/forms';
import { ClickOutsideDirective } from '../../directives';
import { CommonModule } from '@angular/common';

@Component({
  selector: 'custom-calendar',
  standalone: true,
  imports: [FormsModule, ClickOutsideDirective , CommonModule],
  templateUrl: './custom-calendar.component.html',
  styleUrls: ['./custom-calendar.component.css'],
})
export class CustomCalendarComponent {
  @Input() label: string = '';
  @Input() placeholder: string = 'Select date';
  @Input() labelClass: string = '';
  @Input() calendarPopUpClass: string = '';
  @Input() calendarInputClass: string = '';
  @Input() calendarContainerClass: string = '';
  @Input() minDate: Date | null = null;
  @Input() maxDate: Date | null = null;
  @Input({required : true}) value: Date | null = null;
  @Output() valueChange = new EventEmitter<Date | null>();

  showCalendar: boolean = false;
  currentMonth: Date = new Date();
  days: Date[] = [];
  weekdays = ['Su', 'Mo', 'Tu', 'We', 'Th', 'Fr', 'Sa'];

  constructor() {
    this.generateCalendar();
  }

  toggleCalendar(): void {
    this.showCalendar = !this.showCalendar;
  }

  selectDate(date: Date): void {
    this.value = date;
    this.valueChange.emit(date);
    this.showCalendar = false;
  }

  prevMonth(): void {
    this.currentMonth = new Date(
      this.currentMonth.getFullYear(),
      this.currentMonth.getMonth() - 1,
      1
    );
    this.generateCalendar();
  }

  nextMonth(): void {
    this.currentMonth = new Date(
      this.currentMonth.getFullYear(),
      this.currentMonth.getMonth() + 1,
      1
    );
    this.generateCalendar();
  }

  generateCalendar(): void {
    this.days = [];
    const year = this.currentMonth.getFullYear();
    const month = this.currentMonth.getMonth();

    const firstDay = new Date(year, month, 1);
    const lastDay = new Date(year, month + 1, 0);

    // Previous month days
    const prevMonthDays = firstDay.getDay();
    for (let i = prevMonthDays - 1; i >= 0; i--) {
      this.days.push(new Date(year, month, -i));
    }

    // Current month days
    for (let i = 1; i <= lastDay.getDate(); i++) {
      this.days.push(new Date(year, month, i));
    }

    // Next month days
    const nextMonthDays = 6 - lastDay.getDay();
    for (let i = 1; i <= nextMonthDays; i++) {
      this.days.push(new Date(year, month + 1, i));
    }
  }

  isSelected(date: Date): boolean {
    if (!this.value) return false;
    return (
      date.getDate() === this.value.getDate() &&
      date.getMonth() === this.value.getMonth() &&
      date.getFullYear() === this.value.getFullYear()
    );
  }

  isCurrentMonth(date: Date): boolean {
    return date.getMonth() === this.currentMonth.getMonth();
  }
  isDisabled(date: Date): boolean {
    const isBeforeMin = this.minDate ? date < this.minDate : false;
    const isAfterMax = this.maxDate ? date > this.maxDate : false;

    return isBeforeMin || isAfterMax;
  }

  getMonthName(): string {
    return this.currentMonth.toLocaleString('default', { month: 'long' });
  }

  getYear(): number {
    return this.currentMonth.getFullYear();
  }

  formatDisplayDate(): string {
    if (!this.value) return '';
    return this.value.toLocaleDateString();
  }
}
