import { Component, EventEmitter, Input, Output } from '@angular/core';
import { FormsModule } from '@angular/forms';
import { ClickOutsideDirective } from '../../directives';
import { IDropdownOption } from '../../interfaces';

@Component({
  selector: 'custom-multi-select',
  standalone: true,
  imports: [FormsModule, ClickOutsideDirective],
  templateUrl: './custom-multi-select.component.html',
  styleUrls: ['./custom-multi-select.component.css'],
})
export class CustomMultiSelectComponent {
  @Input() label?: string;
  @Input() labelClass: string = '';
  @Input() dropdownOptionsClass: string = '';
  @Input() dropdownHeaderClass: string = '';
  @Input() dropdownContainerClass: string = '';
  @Input() placeholder: string = 'Select options';
  @Input() enableFilter: boolean = false;
  @Input() showClear: boolean = true;
  @Input({ required: true }) options: IDropdownOption[] = [];
  @Input({ required: true }) value: any[] = []; // Array of selected IDs
  @Output() valueChange = new EventEmitter<any[]>();

  isOpen: boolean = false;
  filteredOptions: IDropdownOption[] = [];
  filterText: string = '';

  ngOnInit() {
    this.filteredOptions = [...this.options];
  }

  get selectedOptions(): IDropdownOption[] {
    return this.options.filter((opt) => this.value.includes(opt.id));
  }

  getSelectedLabels(): string {
    return this.selectedOptions.map((opt) => opt.nameEn).join(', ');
  }

  toggleDropdown() {
    this.isOpen = !this.isOpen;
    if (this.isOpen) {
      this.filterText = '';
      this.filterOptions();
    }
  }

  toggleOptionSelection(option: IDropdownOption) {
    if (this.isSelected(option.id)) {
      this.value = this.value.filter((id) => id !== option.id);
    } else {
      this.value = [...this.value, option.id];
    }
    this.valueChange.emit(this.value);
  }

  isSelected(id: any): boolean {
    return this.value.includes(id);
  }

  clearSelection(event: Event) {
    event.stopPropagation();
    this.value = [];
    this.valueChange.emit([]);
  }

  filterOptions() {
    if (!this.filterText) {
      this.filteredOptions = [...this.options];
    } else {
      const searchText = this.filterText.toLowerCase();
      this.filteredOptions = this.options.filter((option) =>
        option.nameEn.toLowerCase().includes(searchText)
      );
    }
  }

  @Input() set reset(value: boolean) {
    if (value) {
      this.value = [];
      this.filterText = '';
      this.filterOptions();
    }
  }
}
