import { TemplateRef } from '@angular/core';
import { IDropdownOption } from './dropdown.interface';

export interface notionPopup<T = any> extends PlanObjectType {
  actionButtons: TemplateRef<T> | null;
}

export interface PlanObjectType extends IDropdownOption {
  startDate: string;
  endDate: string;
  status: PlanStatusEnum;
  priority: planPriorityEnum;
  planTypeNameEn: planTypeNameEnum;
  planTypeNameAr: string;
  zones: IDropdownOption[];
  supervisor: IDropdownOption; // TODO: MISSING FROM BE
  backupSupervisor: IDropdownOption; // TODO: MISSING FROM BE
  progress: number;
  tasks?: Task[]; // TODO: WHEN IMPLEMENTED
  isExpanded?: boolean; // For list expansion state
  attachments: IDropdownOption[];
  description: string;
}

export enum planPriorityEnum {
  LOW = 'LOW',
  MEDIUM = 'MEDIUM',
  HIGH = 'HIGH',
}

export enum planTypeNameEnum {
  WEEKLY = 'WEEKLY',
  CAMPAIGN = 'CAMPAIGN',
}

export enum PlanStatusEnum {
  DRAFT = 'DRAFT',
  ACTIVE = 'ACTIVE',
}

export interface Task {
  name: string;
  assignees: string[];
  startDay: number;
  endDay: number;
  progress: number; // 0-100
}
