import { Injectable, signal } from '@angular/core';

type ToastType = 'success' | 'warning' | 'black' | 'error' | 'info';
type ToastPosition =
  | 'top-right'
  | 'top-left'
  | 'bottom-right'
  | 'bottom-left'
  | 'top-center'
  | 'bottom-center';

@Injectable({
  providedIn: 'root',
})
export class ToastService {
  message = signal('Default message');
  type = signal<ToastType>('info');
  position = signal<ToastPosition>('top-right');
  show = signal(false);

  toast(
    message: string,
    position: ToastPosition,
    ToastType: ToastType = 'info',
    duration?: number
  ) {
    this.message.update(() => message);
    this.type.update(() => ToastType);
    this.position.update(() => position);
    this.showToast();
    if (duration) {
      setTimeout(() => {
        this.hideToast();
      }, duration);
    }
  }

  showToast() {
    this.show.update(() => true);
  }

  hideToast() {
    this.show.update(() => false);
  }
}
