import {
  Component,
  effect,
  inject,
  Input,
  OnInit,
  Output,
} from '@angular/core';
import { ToastService } from '../../services/ui/toast.service';

@Component({
  selector: 'custom-toast',
  imports: [],
  templateUrl: './custom-toast.component.html',
  styleUrl: './custom-toast.component.css',
})
export class CustomToastComponent {
  positionClass: string = 'toast-top-right';
  colorClass: string = 'toast-info';
  toastService = inject(ToastService);

  constructor() {
    effect(() => {
      this.updatePositionClass(this.toastService.position());
      this.updateColorClass(this.toastService.type());
    });
  }

  private updatePositionClass(
    position: typeof this.toastService.position extends () => infer T
      ? T
      : never
  ) {
    switch (position) {
      case 'top-right':
        this.positionClass = 'toast-top-right';
        break;
      case 'top-left':
        this.positionClass = 'toast-top-left';
        break;
      case 'bottom-right':
        this.positionClass = 'toast-bottom-right';
        break;
      case 'bottom-left':
        this.positionClass = 'toast-bottom-left';
        break;
      case 'top-center':
        this.positionClass = 'toast-top-center';
        break;
      case 'bottom-center':
        this.positionClass = 'toast-bottom-center';
        break;
      default:
        this.positionClass = 'toast-top-right';
        break;
    }
  }

  private updateColorClass(
    type: typeof this.toastService.type extends () => infer T ? T : never
  ) {
    switch (type) {
      case 'success':
        this.colorClass = 'toast-success';
        break;
      case 'warning':
        this.colorClass = 'toast-warning';
        break;
      case 'black':
        this.colorClass = 'toast-black';
        break;
      case 'error':
        this.colorClass = 'toast-error';
        break;
      case 'info':
        this.colorClass = 'toast-info';
        break;
      default:
        this.colorClass = 'toast-info';
        break;
    }
  }

  hideToast() {
    this.toastService.hideToast();
  }
}
