import { HttpClient, HttpHeaders } from '@angular/common/http';
import { Inject, Injectable, Optional } from '@angular/core';
import { Observable } from 'rxjs';
import { InjectionToken } from '@angular/core';
import { API_BASE_URL } from '../../helpers-functions/api-base-url.token';

@Injectable({
  providedIn: 'root',
})
export class CommonHttpService {
  constructor(
    private http: HttpClient,
    @Optional() @Inject(API_BASE_URL) private baseUrl?: string
  ) {}

  private buildUrl(endpoint: string): string {
    if (!this.baseUrl) {
      throw new Error('API_BASE_URL is not provided!');
    }
    endpoint = endpoint.startsWith('/') ? endpoint.substring(1) : endpoint;
    return `${this.baseUrl}/${endpoint}`;
  }

  CommonPostRequests(model: any, url: string): Observable<any> {
    return this.http.post<any>(this.buildUrl(url), model);
  }
  CommonPutRequests(model: any, url: string): Observable<any> {
    return this.http.put<any>(this.buildUrl(url), model);
  }
  CommonGetRequests(url: string): Observable<any> {
    return this.http.get<any>(this.buildUrl(url));
  }
  CommonDeleteRequest(url: string): Observable<any> {
    return this.http.delete<any>(this.buildUrl(url));
  }
  CommonGetRequestsWithQuery(url: string, Model: any): Observable<any> {
    if (Model) {
      let queryString = Object.keys(Model)
        .map((key: string) =>
          Model[key] != null && Model[key] != '' && Model[key] != undefined
            ? key + '=' + Model[key]
            : null
        )
        .filter((x) => x != null)
        .join('&');
      url += queryString == '' ? '' : '?' + queryString;
    }
    return this.http.get<any>(this.buildUrl(url));
  }

  CommonPostRequestsWithQuery(
    url: string,
    Model: any,
    body: any
  ): Observable<any> {
    if (Model) {
      let queryString = Object.keys(Model)
        .map((key: string) =>
          (Model[key] != null && Model[key] != '' && Model[key] != undefined) ||
          Model[key] === false
            ? key + '=' + Model[key]
            : null
        )
        .filter((x) => x != null)
        .join('&');
      url += queryString == '' ? '' : '?' + queryString;
    }
    return this.http.post<any>(this.buildUrl(url), body);
  }

  CommonPutRequestsWithQuery(
    url: string,
    Model: any,
    body: any
  ): Observable<any> {
    if (Model) {
      let queryString = Object.keys(Model)
        .map((key: string) =>
          (Model[key] != null && Model[key] != '' && Model[key] != undefined) ||
          Model[key] === false
            ? key + '=' + Model[key]
            : null
        )
        .filter((x) => x != null)
        .join('&');
      url += queryString == '' ? '' : '?' + queryString;
    }
    return this.http.put<any>(this.buildUrl(url), body);
  }
}
